
import projectLayoutImg from "../../images/project-layout-img.jpg"
import projectLayoutImg1 from "../../images/project-layout-img-1.jpg"
import projectLayoutImg2 from "../../images/project-layout-img-2.jpg"
import reviewsCard1 from "../../images/reviews-card-1.svg"
import reviewsCard2 from "../../images/reviews-card-2.svg"
const ProjectLayout = () =>{
    return(
        <section class="project-layout">
            <div class="project-layout__poster-wrapper">
              <span class="bg-color bg-color--theme-darkgrey bg-color--opacity-25"></span>
              <picture class="project-layout__poster">
                <img src={projectLayoutImg} alt="Цифровая трансформация в сфере недвижимости" />
              </picture>
            </div>
            <div class="container container--size-md project-layout__container">
              <h1 class="project-layout__title">Цифровая трансформация в сфере недвижимости</h1>
              <article class="row project-layout__card">
                <div class="row__col--4 project-layout__card-col project-layout__card-col--title">
                  <h2 class="project-layout__card-title">О проекте</h2>
                </div>
                <div class="row__col--8 project-layout__card-col project-layout__card-col--content">
                  <div class="project-layout__card-content">
                    <div class="content project-layout__card-text">
                      <p>IT Компания "Сокол" предлагает комплексные решения для вашего бизнеса, помогая вам успешно перенести свой оффлайн бизнес в онлайн-среду. Мы специализируемся на веб-разработке, маркетинге и поддержке IT-инфраструктуры, предоставляя вам все необходимые инструменты и знания для эффективного функционирования вашего бизнеса в онлайн-мире.</p>
                      <p>Проект включает в себя разработку удобного мобильного приложения, облачное хранилище данных, систему онлайн-платежей и высокоэффективную систему управления жильем.</p>
                      <ul>
                        <li>68,9 млн активных ежемесячных пользователей мобильного приложения «СберБанк Онлайн»</li>
                        <li>101,5 млн активных частных клиентов в России</li>
                        <li>2,9 млн активных корпоративных клиентов в России</li>
                      </ul>
                    </div>
                  </div>
                </div>
              </article>
              <article class="row project-layout__card">
                <div class="row__col--4 project-layout__card-col project-layout__card-col--title">
                  <h2 class="project-layout__card-title">Задача</h2>
                </div>
                <div class="row__col--8 project-layout__card-col project-layout__card-col--content">
                  <div class="project-layout__card-content">
                    <div class="content project-layout__card-text">
                      <p>Чтобы сделать Сбер одной из лучших финансовых и технологических компаний в мире, огромное внимание уделяется разработке и модификации цифровых сервисов, входящих в экосистему.</p>
                      <p>Globus — постоянный подрядчик Сбера (Сбербанка) с 2017 года. Наша команда участвовала в разработке и модификации функционала мобильных приложений iOS и Android, помогала создавать продукты для корпоративных клиентов и физических лиц, настраивала сервисы для оказания банковских услуг, страхования, инвестиций, электронной коммерции, здравоохранения и логистики.</p>
                      <p>К началу 2022 года общий объем выполненных по контрактам работ составил более 1,1 млрд руб.</p>
                    </div>
                  </div>
                </div>
              </article>
              <picture class="project-layout__picture">
                <img src={projectLayoutImg1} alt="" />
              </picture>
              <article class="row project-layout__card">
                <div class="row__col--4 project-layout__card-col project-layout__card-col--title">
                  <h2 class="project-layout__card-title">Реализация</h2>
                </div>
                <div class="row__col--8 project-layout__card-col project-layout__card-col--content">
                  <div class="project-layout__card-content">
                    <div class="content project-layout__card-text">
                      <ul>
                        <li>Осуществили модификацию мобильных приложений на базе платформы iOS и Android.</li>
                        <li>Модуль «Автопереводы (регулярные переводы)» программного обеспечения «Сбербанк Онлайн» на базе платформы iOS.</li>
                        <li>Модули «Оформление дополнительной карты в МП СБОЛ», «Сервис блокировки и разблокировки в МП СБОЛ», «Программа лояльности в МП СБОЛ», «МП СБОЛ. Своё дело» ПО «Сбербанк Онлайн» на базе платформы Android.</li>
                        <li>ПО для реализации требований Управления развития технологий депозитарных услуг.</li>
                        <li>ПО для реализации требований Управления NLP-платформы.</li>
                        <li>ПО для развития функционала омникальной криптографической экосистемы.</li>
                        <li>ПО для развития функционала защиты конфиденциальных электронных документов и корреспонденции, а также повышения киберграмотности клиентов.</li>
                        <li>ПО для развития функционала продаж для крупнейшего, крупного и среднего бизнеса.</li>
                        <li>ППО для развития продуктов сети продаж.</li>
                        <li>ПО для реализации требований интеграции с платформой ЕФС для Управления маркетплейсов.</li>
                        <li>ПО для реализации GreenField SmartMarket для Управления маркетплейсов.</li>
                        <li>ПО с использованием инструментов и технологий Java для развития функционала страховых решений для заботы о близких и постпродажного обслуживания и для ипотеки.</li>
                      </ul>
                    </div>
                  </div>
                </div>
              </article>
              <picture class="project-layout__picture">
                <img src={projectLayoutImg2} alt="" />
              </picture>
              <article class="row project-layout__card">
                <div class="row__col--4 project-layout__card-col project-layout__card-col--title">
                  <h2 class="project-layout__card-title">Цитаты</h2>
                </div>
                <div class="row__col--8 project-layout__card-col project-layout__card-col--content">
                  <div class="project-layout__card-content">
                    <div class="project-layout__card-reviews">
                      <div class="reviews-card project-layout__card-reviews-item">
                        <div class="reviews-card__inner">
                          <header class="reviews-card__header">
                            <div class="reviews-card__header-media">
                              <picture class="reviews-card__header-avatar">
                                <img src={reviewsCard1} alt="Павел Иванов" />
                              </picture>
                            </div>
                            <div class="reviews-card__header-content">
                              <p class="reviews-card__header-name">Павел Иванов</p>
                              <p class="reviews-card__header-position">Веб-разработчик</p>
                            </div>
                          </header>
                          <div class="reviews-card__content">
                            <div class="content reviews-card__text">
                              <p>Компаниями "Рантье" и "Сокол" нам удалось воплотить в жизнь проект, который полностью удовлетворил потребности клиента. Мы создали для них веб-платформу, благодаря которой они увидели значительный рост своего онлайн-бизнеса.</p>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="reviews-card project-layout__card-reviews-item">
                        <div class="reviews-card__inner">
                          <header class="reviews-card__header">
                            <div class="reviews-card__header-media">
                              <picture class="reviews-card__header-avatar">
                                <img src={reviewsCard2} alt="Александр Усович" />
                              </picture>
                            </div>
                            <div class="reviews-card__header-content">
                              <p class="reviews-card__header-name">Александр Усович</p>
                              <p class="reviews-card__header-position">Клиент</p>
                            </div>
                          </header>
                          <div class="reviews-card__content">
                            <div class="content reviews-card__text">
                              <p>Благодаря сотрудничеству с "Рантье" и "Сокол" мы успешно перенесли наше бизнес в онлайн-среду. Их команда профессионалов помогла нам с каждым этапом разработки и маркетинга.</p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </article>
            </div>
          </section>
    );
}

export default ProjectLayout;